/* Media Queries */
/* LYRICS */
/* COLORS */
*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  box-sizing: inherit;
  scroll-behavior: smooth;
}

body {
  font-family: "Roboto Slab", serif;
  background-color: #181818;
}

.contenedor {
  max-width: 1400px;
  width: 90%;
  margin: 0 auto;
}

img,
video {
  max-width: 100%;
  height: auto;
  display: block;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

/* Media queries */
.header {
  background-color: #e36d65;
  padding: 2rem 0;
}
.header__flex {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
@media screen and (min-width: 768px) {
  .header__flex {
    flex-direction: row;
    justify-content: space-between;
  }
}
.header__name {
  font-family: "Neonderthaw", cursive;
  font-size: 3rem;
  font-weight: bold;
  color: #181818;
}
.header__name--red {
  color: #fc7970;
}

.nav {
  margin-top: 1rem;
  display: flex;
  align-items: center;
  gap: 2rem;
}
@media screen and (min-width: 768px) {
  .nav {
    margin-top: 0;
  }
}
.nav__link {
  text-decoration: none;
  color: #181818;
  font-size: 1.5rem;
  font-weight: 700;
  transition: color 250ms ease, text-decoration 250ms ease;
  text-transform: uppercase;
}
.nav__link--red {
  color: #fc7970;
}
.nav__link:hover {
  color: #fff2f1;
  text-decoration: underline;
}

.hero {
  position: relative;
}
.hero__arrow {
  position: absolute;
  bottom: 10rem;
  left: 50%;
  color: transparent;
  background-image: linear-gradient(to right, #e36d65 0%, #fff2f1 50%, #e36d65 100%);
  background-clip: text;
  font-size: 4rem;
  animation-name: movArrow;
  animation-timing-function: ease;
  animation-duration: 800ms;
  animation-fill-mode: forwards;
  animation-iteration-count: infinite;
}

@keyframes movArrow {
  0% {
    opacity: 0;
    transform: translateY(-20%);
  }
  50% {
    opacity: 0.5;
    transform: translateY(0);
  }
  100% {
    opacity: 1;
    transform: translateY(20%);
  }
}
/* -------------------- HERO ----------------------- */
h1 {
  margin: 0;
  font-size: calc(2vw + 20px);
  position: absolute;
  left: 10%;
  transform: translateZ(0.1px) scale(1.2);
  font-family: "Roboto Slab", serif;
  color: transparent;
  background-image: linear-gradient(to right, #e36d65 0%, #fff2f1 50%, #e36d65 100%);
  background-clip: text;
}

h1.clone {
  left: 60%;
}

.defilee__main {
  position: relative;
  margin: 0 auto;
}

.defilee__outer {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  perspective: 0.5px;
  transform-style: preserve-3d;
  overflow-x: hidden;
  overflow-y: hidden;
}

.defilee__main,
.defilee__main *,
.defilee__outer,
.defilee__outer * {
  box-sizing: border-box;
}

.defilee__ctnr {
  transform-style: preserve-3d;
  display: inline-flex;
  align-items: center;
  margin: 0 auto;
}

.defilee__div {
  position: relative;
  transform: translate3d(0, -16vh, 0);
  width: 25vw;
  margin: 0 -2vw;
}

.defilee__div--special {
  transform: translateZ(-0.2px) scale(1.4) translateX(30px) translateY(20vh);
}

.defilee__div--special1 {
  transform: translateZ(-0.25px) scale(1.4) translateX(-0.3vw) translateY(0);
}

.defilee__div--special2 {
  transform: translateZ(0.2px) scale(1) translateX(0.5vw) translateY(-25vh);
}

.defilee__div--special3 {
  transform: translateZ(-0.5px) scale(1.5) translateX(-30px) translateY(-30vh);
}

.defilee__div--special4 {
  transform: translateZ(-0.65px) scale(3) translateX(30px) translateY(-10vh);
}

.defilee__ctnr {
  -webkit-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
  will-change: transform;
}

.defilee__ctnr {
  animation: slide 30s linear infinite;
}

@keyframes slide {
  100% {
    -webkit-transform: translate3d(-50%, 0, 0);
    transform: translate3d(-50%, 0, 0);
  }
}
* {
  transform-origin: 50% 50%;
}

.defilee-visually-hidden {
  border: 0 !important;
  clip: rect(0 0 0 0) !important;
  height: 1px !important;
  margin: -1px !important;
  overflow: hidden !important;
  padding: 0 !important;
  position: absolute !important;
  width: 1px !important;
}

@media screen and (min-width: 768px) {
  .defilee-visually-hidden {
    display: none;
  }
}
