/* -------VENTANA MODAL----------- */
.modal {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: 997;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.9);
  opacity: 0;
  /* para la ventana modal */
  pointer-events: none;
  transition: opacity 500ms ease-in-out;
}

.modal:target {
  opacity: 1;
  pointer-events: auto;
}

.modal-container {
  position: relative;
  border: thin solid #e36d65;
  border-radius: 1rem;
  padding: 2rem;
  width: 80%;
  height: 80%;
  background-color: #181818;
  display: flex;
  flex-direction: column;
  justify-content: center;
  overflow: scroll;
  align-items: center;
}

@media (min-width: 768px) {
  .modal-container {
    overflow: hidden;
  }
}
.modal-close {
  position: absolute;
  top: 1rem;
  right: 1rem;
  text-decoration: none;
  font-size: 2rem;
  background-color: #fc867e;
  color: #fff2f1;
  text-align: center;
  border-radius: 50%;
  transition: background-color 300ms linear, transform 300ms ease-in-out;
}

.modal-close:hover {
  background-color: #e36d65;
  transform: scale(1.2);
}

